.navigation {
    margin: 100px 0;

    display: flex;
    a {
        margin: 100px 0;
        padding: 50px;
        position: relative;
        color:blueviolet;
        font-size: 1em;
        font-weight: bold;
        border-bottom:1px solid  ;
        border-color : black;
        
        
    
    
    }

    .nav-active {
        &:hover {
            
                &:before {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    right: 50%;
                    height: 3px;
                    background-color: blue;
                    transform: scaleX(0);
                    transform-origin: bottom left;
            
                    transition: transform 0.3s;
                }
            
                &:after {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    left : 50%;
                    height: 3px;
                    background-color: blue;
                    transform: scaleX(0);
                    transform-origin: bottom right;
            
                    transition: transform 0.3s;
                }
            
                &:hover {
                    &:before {
                        transform: scaleX(1);
                    }
            
                    &:after {
                        transform: scaleX(1);
                    }
                }
            }
            
         }    
        
    
}